<template>
  <div id="activeManage">
    <el-form
      :inline="true"
      :model="activeForm"
      size="medium"
      label-width="100px"
      label-suffix=""
      class="demo-form-inline"
      ref="activeForm"
    >
      <el-form-item label="">
        <el-select v-model="activeForm.isShow">
          <el-option
            v-for="item in statusOption"
            :key="item.value"
            :label="item.status"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="getActiveList" size="medium"></el-button>
      </el-form-item>
    </el-form>
    <el-button
      size="medium"
      style="margin-top:14px;font-size:13px;"
      type="primary"
      @click="addActive"
    ></el-button>
    <el-button
      size="medium"
      style="margin-top:14px;font-size:13px;"
      type="primary"
      @click="editDefaultPage"
    ></el-button>
    <el-table :data="activeList" border style="width: 100%;margin-top:14px;">
      <el-table-column label="" width="70">
        <template slot-scope="scope">
          <span>{{indexMethods(scope.$index)}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="activeName" label=""></el-table-column>
      <el-table-column prop="activeEnroll" label=""></el-table-column>
      <el-table-column prop="enrollment" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <img :src="scope.row.picPath" alt style="width:100%;">
        </template>
      </el-table-column>
      <el-table-column prop="infoAbbr" label=""></el-table-column>
      <el-table-column prop="infoUrl" label="url"></el-table-column>
      <el-table-column prop="startDate" label=""></el-table-column>
      <el-table-column prop="endDate" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span>{{scope.row.isShow=="0"?"":""}}</span>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="">
        <template slot-scope="scope">
          <el-button
            @click="checkPersonnel(scope.row.id,'checked')"
            type="text"
            size="small"
          ></el-button>
          <el-button @click="deleteActive(scope.row,'checked')" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      background
      layout="prev, pager, next"
      :page-size="activeForm.size"
      :current-page="activeForm.page"
      @current-change="handlePageNumChange"
      :total="total"
    ></el-pagination>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible" :modal-append-to-body="false">
      <el-form>
        <el-form-item label="url" label-width="120px">
          <el-input v-model.trim="url" placeholder="" disabled></el-input>
          <el-upload
            ref="uploadHtml"
            :action="actions"
            :data="formData"
            :auto-upload="true"
            :multiple="false"
            :limit="1"
            accept=".html"
            :before-upload="beforeUploadFile"
            :on-success="handleSuccess"
            :on-remove="removeImg"
          >
            <el-button size="small" plain></el-button>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="upDatePage"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from "vuex";
import http from "http/activeApi";
import https from "http/swiperManageApi";
export default {
  data() {
    return {
      activeForm: {
        isShow: ``,
        page: 1,
        size: 10,
        access_token: ``
      },
      activeList: [],
      total: 0,
      statusOption: [
        { value: "0", status: "" },
        { value: "1", status: "" }
      ],
      dialogFormVisible: false,
      url: ``,
      actions: `https://up.qiniup.com`,
      qiniuaddr: "pd6rnk9ck.bkt.clouddn.com",
      formData: {
        token: ``
      }
    };
  },
  computed: {
    ...mapState(`user`, [`access_token`])
  },
  activated() {
    this.activeForm.access_token = this.access_token;
    this.getActiveList();
  },
  methods: {
    getActiveList() {
      let params = this.activeForm;
      http.getActiveList({ params }).then(data => {
        if (data.data.code == 0) {
          this.activeList = data.data.data;
          this.total = data.data.total;
        }
      });
    },
    handlePageNumChange(num) {
      this.activeForm.page = num;
      this.getActiveList();
    },
    indexMethods(index) {
      return (this.activeForm.page - 1) * this.activeForm.size + index + 1;
    },
    addActive() {
      this.$router.push({ name: `addActive` });
    },
    deleteActive(row) {
      let params = { access_token: this.access_token, id: row.id };
      let that = this;
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          http.deleteActive({ params }).then(data => {
            console.log(data.data.code);
            if (data.data.code == 0) {
              that.$message(data.data.desc);
              that.activeList = this.activeList.filter(item => {
                return item.id != row.id;
              });
              that.getActiveList();
            } else {
              this.$message(``);
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    checkPersonnel(id) {
      this.$router.push({ name: `enrollRecordsList`, query: { actId: id } });
    },
    editDefaultPage() {
      this.dialogFormVisible = true;
    },
    upDatePage() {
      if (!this.url) {
        this.$message(``);
        return;
      }
      let params = { access_token: this.access_token, pageKey: this.key };
      http
        .upDateDefalutPage(params)
        .then(data => {
          console.log(data.data);
          if (data.data.code == 0) {
            this.$message(data.data.desc);
            this.dialogFormVisible = false;
          } else {
            this.$message(data.data.desc);
          }
        })
        .catch(e => {
          this.$message(``);
        });
    },
    // , , false  Promise  reject
    beforeUploadFile(file) {
      console.log("before upload");
      console.log(file);
      let _this = this;
      let name = file.name;
      let params = { fileName: name, access_token: this.access_token };
      let extension = file.name.substring(file.name.lastIndexOf(".") + 1);
      if (extension !== "html") {
        this.$notify.warning({
          title: "",
          message: `html`
        });
        // return false
      }
      return https.getUploadToken({ params }).then(data => {
        console.log(data.data.data.token);
        _this.formData = { token: data.data.data.token };
        _this.key = data.data.data.key;
      });
    },
    // 
    handleSuccess(res, file, fileList) {
      console.log(res);
      this.$message(``);
      this.url = res.payload.url;
      // this.activeForm.infoUrl = res.payload.url;
    },
    removeImg(file, fileList) {
      console.log(``);
      this.url = ``;
    }
  }
};
</script>
<style lang="stylus" scoped>
.el-pagination {
  margin-top: 20px;
  display: flex;
  justify-content: flex-end;
}
</style>


