<template>
  <div id="addStore">
    <el-form
      class="content"
      :model="storeForm"
      ref="storeForm"
      label-width="120px"
      label-suffix=""
      size="medium"
      :rules="rules"
    >
      <!-- <el-form-item label="">
        <el-input v-model.trim="storeForm.stationHost" placeholder=""></el-input>
      </el-form-item>-->
      <el-form-item label="" prop="stationName">
        <el-input v-model.trim="storeForm.stationName" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="selectedOptions">
        <el-cascader
          size="large"
          :options="options"
          v-model.trim="storeForm.selectedOptions"
          @change="handleChange"
        ></el-cascader>
      </el-form-item>
      <el-form-item label="" prop="detailLocation">
        <el-input v-model.trim="storeForm.detailLocation" placeholder="" v-on:input="getDetail"></el-input>
      </el-form-item>
      <el-form-item label="" prop="stationLocation">
        <el-input v-model.trim="storeForm.stationLocation" placeholder="" disabled></el-input>
      </el-form-item>
      <el-form-item label="" prop="stationLat">
        <div style="display:flex;align-items:center;">
          <el-input v-model.trim="storeForm.stationLat" placeholder="" disabled></el-input>
          <el-button type="primary" @click="selectAddress(pageType)" style="margin-left:20px;"></el-button>
        </div>
      </el-form-item>
      <el-form-item label="" prop="stationLon">
        <el-input v-model.trim="storeForm.stationLon" placeholder="" disabled></el-input>
      </el-form-item>
      <el-form-item label="" prop="hostName">
        <el-input v-model.trim="storeForm.hostName" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="contactWay">
        <el-input v-model.trim="storeForm.contactWay" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="cardId">
        <el-input v-model.trim="storeForm.cardId" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="contractNo">
        <el-input v-model.trim="storeForm.contractNo" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="startTime">
        <el-time-picker
          v-model="storeForm.startTime"
          format="HH:mm"
          value-format="HH:mm"
          placeholder=""
        ></el-time-picker>
      </el-form-item>
      <el-form-item label="" prop="endTime">
        <el-time-picker
          v-model="storeForm.endTime"
          format="HH:mm"
          value-format="HH:mm"
          placeholder=""
        ></el-time-picker>
      </el-form-item>
      <el-form-item label="" v-if="pageType=='edit'">
        <el-select v-model="storeForm.stationStatus" clearable>
          <!-- :key="item.value" :label="item.sex" :value="item.value" -->
          <el-option
            v-for="item in storeStatusOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <!-- <el-button type="primary" @click="selectAddress"></el-button> -->

        <el-button type="primary" @click="submitForm('storeForm')"></el-button>
        <el-button @click="resetForm('storeForm')"></el-button>
        <el-button @click="$router.push({name:`storeManage`})"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import { mapState } from "vuex";
import http from "http/storeApi";
import { regionData, CodeToText } from "element-china-area-data";
export default {
  data() {
    return {
      storeForm: {
        stationName: ``, //
        stationLocation: ``, //
        stationLat: ``, //
        stationLon: ``, //
        cardId: ``, //
        contractNo: ``, //
        startTime: ``, //
        endTime: ``, //
        access_token: ``,
        contactWay: ``, //
        hostName: ``, //
        nation: "",
        province: "",
        city: "",
        county: "",
        detailLocation: "",
        selectedOptions:[],
      },
      options: regionData,
      selectedOptions: [],

      pageType: `add`,
      storeStatusOptions: [
        { value: 1, label: `` },
        { value: 2, label: `` },
        { value: 3, label: `` }
      ]
    };
  },
  activated() {
    this.storeForm.access_token = this.access_token;
    console.log(regionData);
    if (this.$route.query.storeInfo) {
      this.storeForm = JSON.parse(this.$route.query.storeInfo);
      this.storeForm.access_token = this.access_token;
      console.log(CodeToText);
      let arr = regionData.filter(item => {
        return item.label == this.storeForm.province;
      });
      console.log(arr);
      let arr1 = arr[0].children.filter(item => {
        return item.label == this.storeForm.city;
      });
      console.log(arr1);
      let arr2 = arr1[0].children.filter(item => {
        return item.label == this.storeForm.county;
      });
      console.log(arr2);
      this.storeForm.selectedOptions = [arr[0].value, arr1[0].value, arr2[0].value];
    }
    if (this.$route.query.addressInfo) {
      console.log(this.$route.query.addressInfo);
      let addressInfo = JSON.parse(this.$route.query.addressInfo);
      this.storeForm.stationLat = addressInfo.lat;
      this.storeForm.stationLon = addressInfo.lng;
      // this.pageType = this.pageType
    }
    this.pageType = this.$route.query.pageType;
  },
  methods: {
    getDetail(value) {
      console.log(value);
      this.storeForm.stationLocation =
        this.storeForm.nation +
        this.storeForm.province +
        this.storeForm.city +
        this.storeForm.county +
        this.storeForm.detailLocation;
    },
    handleChange(value) {
      console.log(value);
      let data =
        CodeToText[value[0]] +
        ", " +
        CodeToText[value[1]] +
        ", " +
        CodeToText[value[2]];
      this.storeForm.province = CodeToText[value[0]];
      this.storeForm.city = CodeToText[value[1]];
      this.storeForm.county = CodeToText[value[2]];
      this.storeForm.stationLocation =
        this.storeForm.nation +
        CodeToText[value[0]] +
        CodeToText[value[1]] +
        CodeToText[value[2]];
      console.log(data);
    },
    submitForm(form) {
      let { storeForm, pageType } = this;
      let that = this;
      this.$refs[form].validate(valid => {
        if (valid) {
          if (pageType == "add") {
            http.addStore(storeForm).then(data => {
              if (data.data.code == 0) {
                console.log(data);
                that.$router.push({ name: `storeManage` });
                that.$message(``);
              } else {
                that.$message.error(data.data.desc);
              }
            });
          } else if (pageType == "edit") {
            http.editStore(storeForm).then(data => {
              if (data.data.code == 0) {
                console.log(data);
                that.$router.push({ name: `storeManage` });
                that.$message(``);
              } else {
                that.$message.error(data.data.desc);
              }
            });
          }
        } else {
          this.$message.error(``);
          return false;
        }
      });
    },
    resetForm() {
      this.storeForm = {
        stationName: ``, //
        stationLocation: ``, //
        stationLat: ``, //
        stationLon: ``, //
        cardId: ``, //
        contractNo: ``, //
        startTime: ``, //
        endTime: ``, //
        access_token: this.access_token,
        contactWay: ``, //
        hostName: ``, //
        selectedOptions:[]
      };
      this.selectedOptions = [];
    },
    selectAddress(pageType) {
      this.$router.push({ name: `selectAddress`, query: { pageType } });
    }
  },
  computed: {
    rules() {
      return {
        selectedOptions: [
          { required: true, message: "", trigger: "change" }
        ],
        hostName: [
          { required: true, message: "", trigger: "blur" }
        ],
        contactWay: [
          { required: true, message: "", trigger: "blur" }
        ],
        stationName: [
          { required: true, message: "", trigger: "blur" }
        ],
        stationLocation: [
          { required: true, message: "", trigger: "blur" }
        ],
        stationLat: [
          { required: true, message: "", trigger: "blur" }
        ],
        stationLon: [
          { required: true, message: "", trigger: "blur" }
        ],
        cardId: [
          { required: true, message: "", trigger: "blur" }
        ],
        contractNo: [
          { required: true, message: "", trigger: "blur" }
        ],
        startTime: [
          { required: false, message: "", trigger: "change" }
        ],
        endTime: [
          { required: false, message: "", trigger: "change" }
        ]
      };
    },
    ...mapState(`user`, [`access_token`])
  }
};
</script>
<style lang="stylus" scoped>
#addStore {
  .el-form {
    margin-top: 40px;
    width: 50%;
  }

  .el-select {
    width: 100%;
  }

  .el-cascader {
    width: 100%;
  }

  .el-time-picker {
    width: 100%;
  }
}
</style>


