<template>
  <div id="comAndSug">
    <el-form
      :inline="true"
      :model="comAndSugForm"
      size="medium"
      label-width="100px"
      label-suffix=""
      class="demo-form-inline"
      ref="comAndSugForm"
    >
      <el-form-item label="">
        <el-select v-model="comAndSugForm.isDeal" clearable>
          <el-option
            v-for="item in typeOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <!-- <el-form-item label="">
        <el-select v-model="comAndSugForm.dealProgress" clearable>
          <el-option
            v-for="item in proGressOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>-->
      <el-form-item style="margin-left:20px;">
        <el-button type="primary" @click="getComAndSugList('clickCheck')" size="medium"></el-button>
      </el-form-item>
    </el-form>
    <el-table :data="comAndSugList" border style="width: 100%;margin-top:14px;">
      <el-table-column label="" width="52">
        <template slot-scope="scope">
          <span>{{indexMethods(scope.$index)}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="userId" label="id"></el-table-column>
      <el-table-column prop="userName" label=""></el-table-column>

      <el-table-column label="">
        <template slot-scope="scope">
          <!-- <span>{{scope.row.headPic}}</span> -->
          <img :src="scope.row.portraitUrl" alt style="width:100%">
        </template>
      </el-table-column>
      <el-table-column prop="csContent" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <!-- <span>{{scope.row.headPic}}</span> -->
          <img
            v-for="(item,index) in scope.row.picturePaths"
            :src="item"
            :key="index"
            style="width:100%"
          >
        </template>
      </el-table-column>
      <el-table-column prop="phoneEmail" label=""></el-table-column>
      <el-table-column prop="createTime" label=""></el-table-column>
      <el-table-column prop="dealTime" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span>{{scope.row.dealProgress==0?'':''}}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span>{{scope.row.isDeal==0?'':''}}</span>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="">
        <template slot-scope="scope">
          <el-button
            v-if="comAndSugForm.dealProgress==0"
            @click="commitDeal(scope.row,'checked')"
            type="text"
            size="small"
          ></el-button>
          <el-button
            @click="editType(scope.row,'checked')"
            type="text"
            size="small"
            :disabled="scope.row.isDeal==1?true:false"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-pagination
      background
      layout="prev, pager, next"
      :page-size="comAndSugForm.size"
      :current-page="comAndSugForm.page"
      @current-change="handlePageNumChange"
      :total="total"
    ></el-pagination>
  </div>
</template>
<script>
import { mapState } from "vuex";
import http from "http/comAndSug";
export default {
  data() {
    return {
      comAndSugForm: {
        access_token: ``,
        page: 1,
        size: 10,
        isDeal: "",
        dealProgress: ``
      },
      total: 0,
      comAndSugList: [],
      typeOptions: [{ value: 0, label: "" }, { value: 1, label: "" }],
      proGressOptions: [
        { value: 0, label: "" },
        { value: 1, label: "" }
      ]
    };
  },
  computed: {
    ...mapState(`user`, [`access_token`, `roleArr`])
  },
  activated() {
    this.comAndSugForm.access_token = this.access_token;
    console.log(this.roleArr.indexOf("ROLE_op-zy") == -1);
    console.log(this.roleArr);
    if (this.roleArr.indexOf("ROLE_op-zy") > -1) {
      this.comAndSugForm.dealProgress = `0`;
    } else if (
      this.roleArr.indexOf("ROLE_af-zy") > -1 ||
      this.roleArr.indexOf("ROLE_af-zg") > -1 ||
      this.roleArr.indexOf("ROLE_af-jl") > -1
    ) {
      this.comAndSugForm.dealProgress = `1`;
    } else {
      this.comAndSugForm.dealProgress = ``;
    }
    this.getComAndSugList();
  },
  methods: {
    getComAndSugList() {
      let params = this.comAndSugForm;
      http.getComAndSugList({ params }).then(data => {
        console.log(data.data.data);
        this.comAndSugList = data.data.data;
        this.total = data.data.total;
      });
    },
    indexMethods(index) {
      return (
        (this.comAndSugForm.page - 1) * this.comAndSugForm.size + index + 1
      );
    },
    handlePageNumChange(num) {
      this.comAndSugForm.page = num;
      this.getComAndSugList();
    },
    editType(row) {
      this.$router.push({ name: `editComAndSugType`, query: { id: row.id } });
    },
    commitDeal(row) {
      let params = { access_token: this.access_token, id: row.id };
      let that = this
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          http.commitDeal(params).then(data => {
            if (data.data.code == 0) {
              that.$message(data.data.desc);
              this.getComAndSugList();
            } else {
              that.$message(data.data.desc);
            }
          });
        })
        .catch(() => {});
    }
  }
};
</script>
<style lang="stylus" scoped>
.el-pagination {
  margin-top: 20px;
  display: flex;
  justify-content: flex-end;
}
</style>


