<template>
  <div id="productDetail">
    <div class="productDetail">
      <div class="product_detail">
        <h3></h3>
        <div class="product-info-content">
          <span></span>
          <span>{{productInfo.gmtCreate}}</span>
        </div>
        <div class="product-info-content">
          <span></span>
          <span>{{productInfo.orderNo}}</span>
        </div>
        <div class="product-info-content">
          <span></span>
          <span>{{productInfo.status=='NO_PAY'?'':productInfo.status=='NO_RESERVE'?'':productInfo.status=='USER_RESERVE'?'':productInfo.status=='NO_INSTALL'?'':productInfo.status=='NO_COMMENT'?'':productInfo.status=='FINISH'?'':productInfo.status=='CANCEL'?'':productInfo.status == 'TIMEOUT'?'':productInfo.status=='REFUNDED'?'':productInfo.status=='REFUNDING'?'':''}}</span>
        </div>
        <div class="product-info-content">
          <span></span>
          <span>{{productInfo.invoiceStatus=='NO'?'':productInfo.invoiceStatus=='ING'?'':productInfo.invoiceStatus=='YES'?'':''}}</span>
        </div>
        <div class="product-info-content" v-if="productInfo.goods">
          <span></span>
          <span>{{productInfo.goods[0].goodsName}}</span>
        </div>
        <div class="product-info-content" v-if="productInfo.goods">
          <span></span>
          <span>{{productInfo.goods[0].goodsModel}}</span>
        </div>
        <div class="product-info-content" v-if="productInfo.goods">
          <span></span>
          <img :src="productInfo.goods[0].goodsImg" class="product-img" />
        </div>
        <div class="product-info-content" v-if="productInfo.goods">
          <span></span>
          <span>{{productInfo.goods[0].originPrice}}</span>
        </div>
        <div class="product-info-content" v-if="productInfo.goods">
          <span></span>
          <span>{{productInfo.goods[0].goodsPrice}}</span>
        </div>
        <h3></h3>
        <div class="product-info-content">
          <span></span>
          <span>{{userInfo.nickname}}</span>
        </div>
        <div class="product-info-content">
          <span></span>
          <span>{{userInfo.mobile}}</span>
        </div>
        <!--  -->
        <div v-if="storeStatus">
          <h3></h3>
          <div class="product-info-content">
            <span></span>
            <span>{{storeInfo.bookTime}}</span>
          </div>
          <div class="product-info-content">
            <span></span>
            <span>{{storeInfo.stationId}}</span>
          </div>
          <div class="product-info-content">
            <span></span>
            <span>{{storeInfo.stationName}}</span>
          </div>
        </div>
        <!--  -->
        <div v-if="installStatus">
          <h3></h3>
          <div class="product-info-content">
            <span></span>
            <span>{{installInfo.confirmTime}}</span>
          </div>
          <div class="product-info-content">
            <span></span>
            <span>{{installInfo.completeTime}}</span>
          </div>
          <div class="product-info-content">
            <span></span>
            <span>{{installInfo.taskId}}</span>
          </div>
          <div class="product-info-content">
            <span></span>
            <span>{{installInfo.stationId}}</span>
          </div>
          <div class="product-info-content">
            <span></span>
            <span>{{installInfo.grabTech}}</span>
          </div>
          <div class="product-info-content">
            <span></span>
            <span>{{installInfo.cooperStatus==0?'':''}}</span>
          </div>
          <div class="product-info-content">
            <span></span>
            <span>{{installInfo.confirmedNum}}</span>
          </div>
          <div class="product-info-content">
            <span></span>
            <span>{{installInfo.takeType==0?'':''}}</span>
          </div>
          <div class="product-info-content">
            <span></span>
            <span>{{installInfo.taskStatus==1?'':installInfo.taskStatus==2?'':installInfo.taskStatus==3?'':installInfo.taskStatus==4?'':installInfo.taskStatus==5?'':installInfo.taskStatus==7?'':''}}</span>
          </div>
          <div class="product-info-content">
            <span></span>
            <img :src="installInfo.driverLicense" alt style="width:200px;" />
          </div>
          <div
            class="product-info-content"
            style="margin-top:20px;"
            v-if="installInfo.pictures&&installInfo.pictures.length!=0"
          >
            <span></span>
            <img
              v-for="item in installInfo.pictures"
              :src="item.picturePath"
              alt
              :key="item.id"
              style="width:200px;margin-right:20px;"
            />
          </div>
          <div
            class="product-info-content"
            v-if="installInfo.technicians&&installInfo.technicians.length!=0"
          >
            <span></span>
            <div
              v-for="item in installInfo.technicians"
              :key="item.id"
              style="display:flex;flex-direction:column;margin-right:34px;"
            >
              <span>id{{item.techId}}</span>
              <span>{{item.techName}}</span>
              <span>{{item.confirmStatus==0?'':''}}</span>
            </div>
          </div>
        </div>
      </div>
      <!--  -->
      <div class="commission">
        <div class="management">
          <h4></h4>
          <p>
            <span>{{management?management.nickname:''}}</span>
            <span>{{management?management.memberId:''}}</span>
          </p>
          <el-table
            :data="management?management.tradeDetails:[]"
            border
            style="width: 100%;margin-top:14px;"
          >
            <el-table-column prop="tradeType" label=""></el-table-column>
            <el-table-column prop="tradeInOut" label="/"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <span>{{scope.row.tradeDesc}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="status" label=""></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <span>{{splitNum(scope.row.amount)}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="gmtCreate" label="" width="160"></el-table-column>
          </el-table>
        </div>
        <!--  -->
        <div class="management">
          <h4></h4>
          <p>
            <span>{{saleInfo?saleInfo.nickname:''}}</span>
            <span>{{saleInfo?saleInfo.memberId:''}}</span>
          </p>
          <el-table
            :data="saleInfo?saleInfo.tradeDetails:[]"
            border
            style="width: 100%;margin-top:14px;"
          >
            <el-table-column prop="tradeType" label=""></el-table-column>
            <el-table-column prop="tradeInOut" label="/"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <span>{{scope.row.tradeDesc}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="status" label=""></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <span>{{splitNum(scope.row.amount)}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="gmtCreate" label="" width="160"></el-table-column>
          </el-table>
        </div>
        <div class="management">
          <h4></h4>
          <p>
            <span>{{stationEarning?stationEarning.stationName:''}}</span>
            <!-- <span>ID{{stationEarning?stationEarning.stationId:''}}</span> -->
          </p>
          <el-table
            :data="stationEarning?stationEarning.records:[]"
            border
            style="width: 100%;margin-top:14px;"
          >
            <el-table-column label="">
              <template slot-scope="scope">
                <span>{{scope.row.earnType==0?'':scope.row.earnType==1?'':scope.row.earnType==3?'':scope.row.earnType==4?'':scope.row.earnType==5?'':''}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="tradeInOut" label="/">
              <template slot-scope="scope">
                <span>{{scope.row.inOut==0?'':''}}</span>
              </template>
            </el-table-column>
            <!-- <el-table-column label="">
              <template slot-scope="scope">
                <span>{{scope.row.earnStatus==0?'':scope.row.earnStatus==1?'':scope.row.earnStatus==2?'':scope.row.earnStatus==3?'':scope.row.earnStatus==4?'':''}}</span>
              </template>
            </el-table-column> -->
            <el-table-column prop="statusDesc" label=""></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <span>{{splitNum(scope.row.earning)}}</span>
              </template>
            </el-table-column>
            <el-table-column prop="createTime" label="" width="160"></el-table-column>
          </el-table>
        </div>
        <div>
          <h4></h4>
          <el-table :data="techEarnings" border style="width: 100%; margin-top: 20px">
            <el-table-column prop="techName" label="" width="110"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-table :data="scope.row.records?scope.row.records:[]">
                  <el-table-column label="">
                    <template slot-scope="scope1">
                      <span>{{scope1.row.earnType==0?'':scope1.row.earnType==1?'':scope1.row.earnType==3?'':scope1.row.earnType==4?'':scope1.row.earnType==5?'':''}}</span>
                    </template>
                  </el-table-column>
                  <el-table-column prop="tradeInOut" label="/">
                    <template slot-scope="scope">
                      <span>{{scope.row.inOut==0?'':''}}</span>
                    </template>
                  </el-table-column>
                  <!-- <el-table-column label="">
                    <template slot-scope="scope">
                      <span>{{scope.row.earnStatus==0?'':scope.row.earnStatus==1?'':scope.row.earnStatus==2?'':scope.row.earnStatus==3?'':scope.row.earnStatus==4?'':''}}</span>
                    </template>
                  </el-table-column> -->
                  <el-table-column prop="statusDesc" label=""></el-table-column>
                  <el-table-column label="">
                    <template slot-scope="scope">
                      <span>{{splitNum(scope.row.earning)}}</span>
                    </template>
                  </el-table-column>
                  <el-table-column prop="createTime" label="" width="160"></el-table-column>
                </el-table>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
    <div style="text-align: center;margin-top: 12px;">
      <el-button
        type="primary"
        :disabled="productInfo.sharedProfit"
        @click="makeMoney"
        size="medium"
        v-if="productInfo.status=='NO_COMMENT'||productInfo.status=='FINISH'"
      >{{productInfo.sharedProfit==false?'':''}}</el-button>
      <el-button
        type="primary"
        @click="getStoreInfo"
        size="medium"
        v-if="productInfo.status=='NO_COMMENT'||productInfo.status=='FINISH'||productInfo.status=='USER_RESERVE'||productInfo.status=='NO_INSTALL'"
      ></el-button>
      <el-button
        type="primary"
        @click="getInstallInfo"
        size="medium"
        v-if="productInfo.status=='NO_COMMENT'||productInfo.status=='FINISH'||productInfo.status=='NO_INSTALL'"
      ></el-button>
      <el-button
        type="primary"
        @click="assignStore"
        size="medium"
        v-if="productInfo.status=='NO_RESERVE'"
      ></el-button>
      <el-button type="primary" @click="$router.go(-1)" size="medium"></el-button>
    </div>
  </div>
</template>
<script>
import http from "http/orderManageApi";
import https from "http/loginApi";
import { mapState } from "vuex";
export default {
  name: `productDetail`,
  data() {
    return {
      productInfo: {},
      memberId: ``,
      userInfo: {},
      storeInfo: {},
      installInfo: {},
      storeStatus: false,
      installStatus: false,
      saleInfo: {},
      management: {},
      stationEarning: {}, //
      techEarnings: [] //
    };
  },
  activated() {
    this.productInfo = JSON.parse(this.$route.query.productInfo);
    this.memberId = JSON.parse(this.$route.query.productInfo).memberId;
    this.getUserInfo();
    this.storeStatus = false;
    this.installStatus = false;
    this.getCommission();
    this.getIncomeRecord();
  },
  computed: {
    ...mapState(`user`, [`access_token`])
  },
  methods: {
    getUserInfo() {
      let params = { memberId: this.memberId, access_token: this.access_token };
      https.getUserInfo({ params }).then(data => {
        console.log(data.data);
        this.userInfo = data.data.data;
      });
    },
    // 
    splitNum(num) {
      if (typeof num !== "number" || isNaN(num)) return null;
      return num / 100.0;
    },
    makeMoney() {
      let params = {
        access_token: this.access_token,
        orderNo: this.productInfo.orderNo
      };
      let that = this;
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          http.makeMoney(params).then(data => {
            console.log(data.data);
            if (data.data.code == 0) {
              that.$message(``);
              that.$router.go(-1);
            }
          });
          // this.$message({
          //   type: 'success',
          //   message: '!'
          // });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    getStoreInfo() {
      let params = {
        access_token: this.access_token,
        orderNo: this.productInfo.orderNo
      };
      http.getStoreInfo({ params }).then(data => {
        console.log(data.data.data);
        if (data.data.code == 0) {
          this.storeInfo = data.data.data;
          this.storeStatus = true;
        } else {
          this.$message(data.data.desc);
        }
      });
    },
    objectSpanMethod({ row, column, rowIndex, columnIndex }) {},
    getInstallInfo() {
      let params = {
        access_token: this.access_token,
        orderNo: this.productInfo.orderNo
      };
      http.getInstallInfo({ params }).then(data => {
        console.log(data.data);
        if (data.data.code == 0) {
          this.installInfo = data.data.data;
          this.installStatus = true;
        } else {
          this.$message(data.data.desc);
        }
      });
    },
    assignStore() {
      let userInfo = this.productInfo.orderUserInfo;
      let orderInfo = this.productInfo;
      this.$router.push({
        name: `assignStore`,
        query: {
          userInfo: JSON.stringify(userInfo),
          orderInfo: JSON.stringify(orderInfo)
        }
      });
    },
    //
    getCommission() {
      let params = {
        orderNo: this.productInfo.orderNo,
        access_token: this.access_token
      };
      http.getCommission({ params }).then(data => {
        console.log(data.data);
        this.management = data.data.data.management;
        this.saleInfo = data.data.data.sale;
      });
    },
    //
    getIncomeRecord() {
      let params = {
        orderNo: this.productInfo.orderNo,
        access_token: this.access_token
      };
      http.getIncomeRecord({ params }).then(data => {
        console.log(data);
        if (data.data.code == 0) {
          this.stationEarning = data.data.data.stationEarning;
          this.techEarnings = data.data.data.techEarnings;
        }
      });
    }
  }
};
</script>
<style lang="stylus" scoped>
.productDetail {
  display: flex;
  // align-items: center;
}

.product_detail {
  width: 50%;
}

.commission {
  flex: 1;
}

.product-info-content {
  line-height: 36px;
  display: flex;
  align-items: center;
}

.management {
  p {
    span {
      margin-right: 60px;
    }
  }
}

.product-info-content>span {
  display: block;
  margin-right: 14px;
}

.product-info-content>span:nth-child(1) {
  min-width: 120px;
}

.productImg {
  width: 178px;
  height: 178px;
  margin-right: 12px;
}
</style>


