<template>
  <div id="storeManage">
    <el-form
      :inline="true"
      :model="storeInfo"
      size="medium"
      label-width="120px"
      label-suffix=""
      class="demo-form-inline"
      ref="storeInfo"
    >
      <el-form-item label="">
        <el-input v-model="storeInfo.stationName" placeholder="" clearable></el-input>
      </el-form-item>
      <!-- <el-form-item label="">
                <el-input v-model="storeInfo.stationLocation" placeholder="" clearable></el-input>
      </el-form-item>-->
      <el-form-item label="">
        <el-input v-model="storeInfo.cardId" placeholder="" clearable></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="storeInfo.contractNo" placeholder="" clearable></el-input>
      </el-form-item>
    </el-form>
    <div class="query-btn">
      <el-button type="primary" @click="getStoreList" size="medium"></el-button>
      <!-- <el-button type="info" @click="resetForm" style="margin-left:40px;" size="medium"></el-button> -->
    </div>
    <el-button size="medium" style="margin-top:14px;" type="primary" @click="addStore"></el-button>
    <!--  -->
    <el-table :data="storeList" border style="width: 100%;margin-top:14px;">
      <el-table-column label="" width="72">
        <template slot-scope="scope">
          <span>{{indexMethods(scope.$index)}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="stationName" label=""></el-table-column>
      <el-table-column prop="stationLocation" label=""></el-table-column>
      <el-table-column prop="stationLon" label=""></el-table-column>
      <el-table-column prop="stationLat" label=""></el-table-column>
      <el-table-column prop="hostName" label=""></el-table-column>
      <el-table-column prop="contactWay" label=""></el-table-column>
      <el-table-column prop="cardId" label=""></el-table-column>
      <el-table-column prop="contractNo" label=""></el-table-column>
      <el-table-column prop="startTime" label=""></el-table-column>
      <el-table-column prop="endTime" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span style="display:block;">{{scope.row.creditScore}}</span>
          <el-button @click="scoreDetail(scope.row,'checked')" type="text" size="small"></el-button>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="">
        <template slot-scope="scope">
          <el-button @click="editStore(scope.row,'checked')" type="text" size="small"></el-button>
          <el-button @click="deleteStore(scope.row,'checked')" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <!-- <pagination :page-size="storeInfo.size" :current-page="storeInfo.page"
                :total-count="total"
        @pageNumChange="handlePageNumChange">
    </pagination>-->
    <el-pagination
      background
      layout="prev, pager, next"
      :page-size="storeInfo.size"
      :current-page="storeInfo.page"
      @current-change="handlePageNumChange"
      :total="total"
    ></el-pagination>
  </div>
</template>
<script>
import http from "http/storeApi";
import { mapState } from "vuex";
// import pagination from 'common/pagination.vue'
export default {
  data() {
    return {
      storeInfo: {
        stationName: null, //
        stationLocation: null, //
        cardId: null, //
        contractNo: null, //
        startTime: null, //
        endTime: null, //
        stationStatus: null, //
        authStatus: null, //
        page: 1,
        size: 8,
        access_token: ``
      },
      total: 0, //
      storeList: [],
      sexOptions: [
        {
          value: 0,
          sex: ""
        },
        {
          value: 1,
          sex: ""
        }
      ],
      storeOptions: []
    };
  },
  //  components: {
  //     pagination
  //  },
  activated() {
    this.storeInfo.access_token = this.access_token;
    this.getStoreList();
  },
  computed: {
    ...mapState(`user`, [`access_token`])
  },
  methods: {
    getStoreList() {
      let params = this.storeInfo;
      http.getStoreList(params).then(data => {
        console.log(data.data);
        this.storeList = data.data.data;
        this.total = data.data.total;
      });
    },
    // 
    handlePageNumChange(num) {
      this.storeInfo.page = num;
      this.getStoreList();
    },
    resetForm() {},
    handleClick(row) {},
    //
    addStore() {
      this.$router.push({ name: `addStore`, query: { pageType: "add" } });
    },
    indexMethods(index) {
      return (this.storeInfo.page - 1) * this.storeInfo.size + index + 1;
    },
    deleteStore(row) {
      let that = this;
      let params = { access_token: this.access_token, id: row.id };
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          http.deleteStore({ params }).then(data => {
            console.log(data.data.code);
            if (data.data.code == 0) {
              that.$message(data.data.desc);
              that.storeList = this.storeList.filter(item => {
                return item.id != row.id;
              });
              that.getStoreList();
            } else {
              this.$message(data.data.desc);
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    editStore(row) {
      // let storeInfo = {
      //   stationName: row.stationName, //
      //   stationLocation: row.stationLocation, //
      //   stationLat: row.stationLat, //
      //   stationLon: row.stationLon, //
      //   cardId: row.cardId, //
      //   contractNo: row.contractNo, //
      //   startTime: row.startTime, //
      //   endTime: row.endTime, //
      //   access_token: this.access_token,
      //   contactWay: row.contactWay, //
      //   hostName: row.hostName, //
      //   stationStatus:row.stationStatus
      // }

      this.$router.push({
        name: `addStore`,
        query: { storeInfo: JSON.stringify(row), pageType: "edit" }
      });
    },
    scoreDetail(row){
      this.$router.push({
        name: `scoreDetail`,
        query: {stationIds:row.stationId}
      });
    },
  }
};
</script>
<style lang="stylus" scoped>
#storeManage {
  .el-form {
  }

  .el-form-item {
    margin-bottom: 20px;
  }

  .el-input {
    width: 250px;
  }

  .el-select {
    width: 250px;
  }

  .query-btn {
    text-align: center;
    border-bottom: 1px solid #ededed;
    padding-bottom: 20px;
  }

  .el-pagination {
    margin-top: 20px;
    display: flex;
    justify-content: flex-end;
  }

  .current_page_classname /deep/ .el-table__row {
    td:not(.is-hidden):last-child {
      right: -1px;
    }
  }
}
</style>


