<template>
  <div id="productManage">
    <!-- <el-button size="medium" style="margin-top:14px;" type="primary" @click="addStore"></el-button> -->
    <el-form
      :inline="true"
      :model="productForm"
      size="medium"
      label-width="100px"
      label-suffix=""
      class="demo-form-inline"
      ref="productForm"
    >
      <el-form-item label="">
        <el-select v-model="productForm.status" clearable>
          <el-option
            v-for="item in statusOption"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="productForm.sharedProfit" clearable>
          <el-option
            v-for="item in shareOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker
          v-model="timeArr"
          type="datetimerange"
          value-format="yyyy-MM-dd HH:mm:ss"
          range-separator=""
          start-placeholder=""
          end-placeholder=""
          @change="selectTime"
        ></el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="getProductList" size="medium"></el-button>
      </el-form-item>
    </el-form>
    <el-table :data="productList" border style="width: 100%;margin-top:14px;">
      <el-table-column label="" width="58" fixed="left">
        <template slot-scope="scope">
          <span>{{indexMethods(scope.$index)}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="gmtCreate" label="" width="140"></el-table-column>
      <el-table-column prop="orderNo" label="" width="140"></el-table-column>
      <el-table-column label="" width="140">
        <template slot-scope="scope">
          <span>{{scope.row.status=='NO_PAY'?'':scope.row.status=='NO_RESERVE'?'':scope.row.status=='USER_RESERVE'?'':scope.row.status=='NO_INSTALL'?'':scope.row.status=='NO_COMMENT'?'':scope.row.status=='FINISH'?'':scope.row.status=='CANCEL'?'':scope.row.status == 'TIMEOUT'?'':scope.row.status=='REFUNDED'?'':scope.row.status=='REFUNDING'?'':''}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="140">
        <template slot-scope="scope">
          <span>{{(scope.row.status=='FINISH'||scope.row.status=='NO_COMMENT')&&scope.row.sharedProfit==true?'':(scope.row.status=='FINISH'||scope.row.status=='NO_COMMENT')&&scope.row.sharedProfit==false?'':'-'}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="140">
        <template slot-scope="scope">
          <span>{{scope.row.invoiceStatus=='NO'?'':scope.row.invoiceStatus=='ING'?'':scope.row.invoiceStatus=='YES'?'':''}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="140">
        <template slot-scope="scope">
          <span>{{scope.row.goods[0].goodsName}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="140">
        <template slot-scope="scope">
          <span>{{scope.row.goods[0].goodsModel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="140">
        <template slot-scope="scope">
          <img :src="scope.row.goods[0].goodsImg" class="product-img" />
        </template>
      </el-table-column>

      <el-table-column label="" width="140">
        <template slot-scope="scope">
          <span>{{scope.row.goods[0].originPrice}}</span>
        </template>
        
      </el-table-column>
      <el-table-column label="" width="140">
        <template slot-scope="scope">
          <span>{{scope.row.goods[0].goodsPrice}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="orderUserInfo.name" label="" width="140"></el-table-column>
      <el-table-column prop="orderUserInfo.mobile" label="" width="140"></el-table-column>
      <el-table-column fixed="right" label="" width="140">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row,'checked')" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      background
      layout="prev, pager, next"
      :page-size="productForm.size"
      :current-page="productForm.page"
      @current-change="handlePageNumChange"
      :total="total"
    ></el-pagination>
  </div>
</template>
<script>
import http from "http/orderManageApi";
import { mapState } from "vuex";
export default {
  name: "productManage",
  data() {
    return {
      productList: [],
      timeArr: [],
      productForm: {
        page: 1,
        size: 10,
        access_token: ``,
        status: ``,
        sharedProfit: ``,
        startTime: ``,
        endTime: ``
      },
      statusOption: [
        {
          value: "NO_PAY",
          label: ""
        },
        {
          value: "NO_RESERVE",
          label: ""
        },
        {
          value: "NO_INSTALL",
          label: ""
        },
        {
          value: "NO_COMMENT",
          label: ""
        },
        {
          value: "FINISH",
          label: ""
        },
        {
          value: "CANCEL",
          label: ""
        },
        {
          value: "TIMEOUT",
          label: ""
        },
        {
          value: "REFUNDING",
          label: ""
        },
        {
          value: "REFUNDED",
          label: ""
        },
        {
          value: "USER_RESERVE",
          label: ""
        }
      ],
      shareOptions: [
        {
          value: false,
          label: ``
        },
        {
          value: true,
          label: ``
        }
      ],
      total: 0
    };
  },
  computed: {
    ...mapState(`user`, [`access_token`])
  },
  activated() {
    this.productForm.access_token = this.access_token;
    this.getProductList();
  },
  methods: {
    selectTime(arr) {
      if (arr == null) {
        this.timeArr = [];
        this.productForm.startTime = ``;
        this.productForm.endTime = ``;
      } else {
        this.productForm.startTime = arr[0];
        this.productForm.endTime = arr[1];
      }
    },
    addStore() {
      
      this.$router.push({ name: `addProduct`, query: { pageType: "add" } });
    },
    editProduct(row) {
      let productInfo = JSON.stringify(row);
      this.$router.push({
        name: `addProduct`,
        query: { pageType: "edit", productInfo }
      });
    },
    indexMethods(index) {
      return (this.productForm.page - 1) * this.productForm.size + index + 1;
    },
    getProductList() {
      let params = this.productForm;
      http.getOrderList({ params }).then(data => {
        console.log(data.data.data);
        let arr = data.data.data;
        arr.forEach(item => {
          item.goods[0].originPrice = this.splitNum(item.goods[0].originPrice);
          item.goods[0].goodsPrice = this.splitNum(item.goods[0].goodsPrice);
        });
        this.productList = arr;
        this.total = data.data.total;
      });
    },
    // 
    splitNum(num) {
      if (typeof num !== "number" || isNaN(num)) return null;
      return num / 100.0;
    },
    handlePageNumChange(num) {
      this.productForm.page = num;
      this.getProductList();
    },
    //
    deleteProduct(row) {
      let params = { no: row.no, access_token: this.access_token };
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          http.deleteProduct({ params }).then(data => {
            console.log(data);
            if (data.data.code == 0) {
              this.$message({
                type: "success",
                message: "!"
              });
            } else {
              this.$message({
                type: "success",
                message: data.data.desc
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    // 
    handleClick(row) {
      let productInfo = JSON.stringify(row);
      this.$router.push({ name: `orderDetail`, query: { productInfo } });
    }
  }
};
</script>
<style lang="stylus" scoped>
.product-img {
  width: 100%;
}

.el-pagination {
  margin-top: 20px;
  display: flex;
  justify-content: flex-end;
}
</style>


