<template>
  <div>
    <el-form
      :inline="true"
      :model="leaveInfo"
      size="medium"
      label-width="100px"
      label-suffix=""
      class="demo-form-inline"
      ref="leaveInfo"
    >
      <el-form-item label="">
        <el-date-picker
          v-model="timeArr"
          type="datetimerange"
          value-format="yyyy-MM-dd HH:mm:ss"
          range-separator=""
          start-placeholder=""
          end-placeholder=""
          @change="selectTime"
        ></el-date-picker>
      </el-form-item>
      <el-button type="primary" @click="getTechleaveList" size="medium"></el-button>
    </el-form>
    <el-table :data="leaveList" border style="width: 100%;margin-top:14px;">
      <el-table-column label="" width="58">
        <template slot-scope="scope">
          <span>{{indexMethods(scope.$index)}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="userId" label=""></el-table-column>
      <el-table-column prop="createTime" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span>{{scope.row.authType===2?'':scope.row.authType===3?'':scope.row.authType===6?'':''}}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span>{{scope.row.authStatus===0?'':scope.row.authStatus===1?'':scope.row.authStatus===2?'':scope.row.authStatus===3?'':scope.row.authStatus===4?'':''}}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span>{{scope.row.curStatus===0?'':scope.row.curStatus===1?'':''}}</span>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="">
        <template slot-scope="scope">
          <el-button
            @click="gotoAudit(scope.row)"
            type="text"
            size="small"
            v-if="scope.row.curStatus==0"
          ></el-button>
          <!-- <el-button @click="handleClick(scope.row,'checked')" type="text" size="small"></el-button> -->
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      background
      layout="prev, pager, next"
      :page-size="leaveInfo.size"
      :current-page="leaveInfo.page"
      @current-change="handlePageNumChange"
      :total="total"
    ></el-pagination>
  </div>
</template>
<script>
import { mapState } from "vuex";
import http from "http/approvalManageApi";
export default {
  name: "storeAudit",
  data() {
    return {
      leaveInfo: {
        page: 1,
        size: 8,
        authTypes: [2, 3, 6],
        // authStatus:0
        access_token: ``
      },
      leaveList: [],
      total: 0,
      timeArr:[]
    };
  },
  activated() {
    this.leaveInfo.access_token = this.access_token;
    this.getTechleaveList();
  },
  methods: {
    selectTime(arr) {
      console.log(arr);
      if (arr == null) {
        this.timeArr = [];
        this.leaveInfo.startTime = ``;
        this.leaveInfo.endTime = ``;
      } else {
        this.leaveInfo.startTime = arr[0];
        this.leaveInfo.endTime = arr[1];
      }
    },
    getTechleaveList() {
      // let params = this.leaveInfo;
      let params = { authTypes: this.leaveInfo.authTypes,startTime:this.leaveInfo.startTime,endTime:this.leaveInfo.endTime };
      let value = {
        access_token: this.access_token,
        page: this.leaveInfo.page,
        size: this.leaveInfo.size
      };
      let that = this;
      http.getApplyList(params, value).then(data => {
        console.log(data.data.desc);
        if (data.data.code == 0) {
          this.leaveList = data.data.data;
          this.total = data.data.total;
        } else {
          that.$message(data.data.desc);
        }
      });
    },
    gotoAudit(row) {
      let authContent = row.authContent;
      let applyId = row.id;
      console.log(authContent);
      this.$router.push({
        name: `leaveInfo`,
        query: { authContent, applyId, pageType: "joinOrleave" }
      });
    },
    indexMethods(index) {
      return (this.leaveInfo.page - 1) * this.leaveInfo.size + index + 1;
    },
    handlePageNumChange(num) {
      this.leaveInfo.page = num;
      this.getTechleaveList();
    }
  },
  computed: {
    ...mapState(`user`, [`access_token`])
  }
};
</script>
<style lang="stylus" scoped>
.el-pagination {
  margin-top: 20px;
  display: flex;
  justify-content: flex-end;
}
</style>    


