<template>
  <div id="addPersonnel">
    <el-form
      class="content"
      :model="personnelForm"
      ref="personnelForm"
      label-width="100px"
      label-suffix=""
      size="medium"
      :rules="rules"
    >
      <el-form-item label="" prop="cardId">
        <el-input v-model.trim="personnelForm.cardId" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="techName">
        <el-input v-model.trim="personnelForm.techName" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="sex">
        <el-select v-model="personnelForm.sex" placeholder="">
          <el-option label="" :value="0"></el-option>
          <el-option label="" :value="1"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="techPhone">
        <el-input v-model.trim="personnelForm.techPhone" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="techAddress">
        <el-input v-model.trim="personnelForm.techAddress" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="" prop="certificateNo">
        <el-input v-model.trim="personnelForm.certificateNo" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('personnelForm')"></el-button>
        <el-button @click="resetForm('personnelForm')"></el-button>
        <el-button @click="$router.go(-1)"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import http from "http/personnelApi";
import { mapState } from "vuex";
export default {
  data() {
    return {
      personnelForm: {
        cardId: ``, //
        techName: ``, //
        sex: 0, //
        techPhone: ``, //
        techAddress: ``, //
        certificateNo: ``, //
        access_token: ``
      },
      pageType: ``
    };
  },
  activated() {
    this.personnelForm.access_token = this.access_token;
    if (this.$route.query.personnelInfo) {
      this.personnelForm = JSON.parse(this.$route.query.personnelInfo);
      // this.storeForm.access_token = this.access_token
    }
    this.pageType = this.$route.query.pageType;
  },
  methods: {
    submitForm(formName) {
      let { personnelForm, pageType } = this;
      let that = this;
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (pageType == "add") {
            http.addPersonnel(personnelForm).then(data => {
              console.log(data);
              if (data.data.code == 0) {
                that.$router.back(-1);
                that.$message("");
              } else {
                that.$message(data.data.desc);
              }
            });
          } else {
            http.editPersonnel(personnelForm).then(data => {
              console.log(data);
              if (data.data.code == 0) {
                that.$router.back(-1);
                that.$message("");
              } else {
                that.$message(data.data.desc);
              }
            });
          }
        } else {
          that.$message.error(``);
          return false;
        }
      });
    },
    resetForm() {
      this.personnelForm = {
        cardId: ``, //
        techName: ``, //
        sex: 0, //
        techPhone: ``, //
        techAddress: ``, //
        certificateNo: ``, //
        access_token: this.access_token
      };
    },
  },
  computed: {
      rules() {
        return {
          cardId: [
            { required: true, message: "", trigger: "blur" }
          ],
          techName: [
            { required: true, message: "", trigger: "blur" }
          ],
          sex: [{ required: true, message: "", trigger: "change" }],
          techPhone: [
            { required: true, message: "", trigger: "blur" }
          ],
          techAddress: [
            { required: true, message: "", trigger: "blur" }
          ],
          certificateNo: [
            { required: true, message: "", trigger: "blur" }
          ]
        };
      },
      ...mapState(`user`, [`access_token`])
    }
};
</script>
<style lang="stylus" scoped>
#addPersonnel {
  .el-form {
    margin-top: 40px;
    width: 50%;
  }

  .el-select {
    width: 100%;
  }

  .el-cascader {
    width: 100%;
  }
}
</style>


