<template>
  <div id="personnelManage">
    <el-form
      :inline="true"
      :model="userInfo"
      size="medium"
      label-width="100px"
      label-suffix=""
      class="demo-form-inline"
      ref="userInfo"
    >
      <el-form-item label="">
        <el-input v-model="userInfo.cardId" placeholder="" clearable></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="userInfo.techName" placeholder="" clearable></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="userInfo.sex" clearable>
          <el-option
            v-for="item in sexOptions"
            :key="item.value"
            :label="item.sex"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="userInfo.techPhone" placeholder="" clearable></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="userInfo.stationId" clearable>
          <!-- :key="item.value" :label="item.sex" :value="item.value" -->
          <el-option
            v-for="item in storeOptions"
            :key="item.stationId"
            :label="item.stationName"
            :value="item.stationId"
          ></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div class="query-btn">
      <el-button type="primary" @click="getDealerList" size="medium"></el-button>
      <!-- <el-button type="info" @click="resetForm" style="margin-left:40px;" size="medium"></el-button> -->
    </div>
    <el-button size="medium" style="margin-top:14px;" type="primary" @click="addUser"></el-button>
    <!--  -->
    <el-table :data="technicianList" border style="width: 100%;margin-top:14px;">
      <el-table-column label="" width="58">
        <template slot-scope="scope">
          <span>{{indexMethods(scope.$index)}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="techId" label=""></el-table-column>
      <el-table-column prop="cardId" label=""></el-table-column>
      <el-table-column prop="techName" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span>{{scope.row.sex==0?'':''}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="techPhone" label=""></el-table-column>
      <el-table-column prop="techAddress" label=""></el-table-column>
      <el-table-column prop="certificateNo" label=""></el-table-column>
      <el-table-column fixed="right" label="">
        <template slot-scope="scope">
          <el-button @click="editPersonnel(scope.row,'checked')" type="text" size="small"></el-button>
          <el-button @click="deletePersonel(scope.row,'checked')" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      background
      layout="prev, pager, next"
      :page-size="userInfo.size"
      :current-page="userInfo.page"
      @current-change="handlePageNumChange"
      :total="total"
    ></el-pagination>
  </div>
</template>
<script>
import { mapState } from "vuex";
import http from "http/personnelApi";
export default {
  data() {
    return {
      userInfo: {
        cardId: null, //
        techName: null, //
        sex: null, // 0  1
        techPhone: null, //
        stationId: null, //
        page: 1,
        size: 10,
        access_token: `` //TODO 
      },
      total: 0,
      technicianList: [],
      sexOptions: [
        {
          value: 0,
          sex: ""
        },
        {
          value: 1,
          sex: ""
        }
      ],
      storeOptions: []
    };
  },
  activated() {
    this.userInfo.access_token = this.access_token;
    this.getDealerList();
    this.getStoreList();
  },
  computed: {
    ...mapState(`user`, [`access_token`])
  },
  methods: {
    getDealerList() {
      let params = this.userInfo;
      http.getPersonnelList(params).then(data => {
        console.log(data);
        this.technicianList = data.data.data;
        this.total = data.data.total;
      });
    },
    //
    getStoreList() {
      console.log();
      let params = { page: 1, size: 0, access_token: this.access_token };
      http.storeList({ params }).then(data => {
        console.log(data);
        this.storeOptions = data.data.data;
      });
    },
    resetForm() {
      this.userInfo = {
        cardId: null, //
        techName: null, //
        sex: null, // 0  1
        techPhone: null, //
        stationId: null, //
        page: 1,
        size: 10,
        access_token: this.access_token //TODO 
      };
    },
    handleClick(row) {},
    //
    addUser() {
      this.$router.push({ name: `addPersonnel`,query:{pageType:`add`} });
    },
    //
    editPersonnel(row){
      let personnelInfo = {
        cardId: row.cardId, //
        techName: row.techName, //
        sex: row.sex, //
        techPhone: row.techPhone, //
        techAddress: row.techAddress, //
        certificateNo: row.certificateNo, //
        techId:row.techId,
        access_token: this.access_token
      }
      this.$router.push({name:`addPersonnel`,query:{personnelInfo:JSON.stringify(personnelInfo),pageType:'edit'}})
    },
    indexMethods(index) {
      return (this.userInfo.page - 1) * this.userInfo.size + index + 1;
    },
    handlePageNumChange(num) {
      this.userInfo.page = num;
      this.getDealerList();
    },
    deletePersonel(row){
      let that = this
      let params = {access_token:this.access_token,id:row.id}
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          http.deletePersonnel({ params })
            .then(data => {
                console.log(data.data.code)
              if (data.data.code == 0) {
                that.$message(data.data.desc);
                that.technicianList = this.technicianList.filter(item => {
                  return item.id != row.id;
                });
                that.getDealerList();
              } else {
                this.$message(data.data.desc);
              }
            })
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  }
};
</script>
<style lang="stylus" scoped>
#personnelManage {
  .el-form {
  }

  .el-form-item {
    margin-bottom: 20px;
  }

  .el-input {
    width: 250px;
  }

  .el-select {
    width: 250px;
  }

  .query-btn {
    text-align: center;
    border-bottom: 1px solid #ededed;
    padding-bottom: 20px;
  }

  .el-pagination {
    margin-top: 20px;
    display: flex;
    justify-content: flex-end;
  }
}
</style>


