<template>
  <div class="invoiceAudit">
    <div class="invoice-info">
      <h4></h4>
      <div class="invoice-info-content">
        <span></span>
        <span>{{invoiceInfo.orderNo}}</span>
      </div>
      <div class="invoice-info-content">
        <span></span>
        <span>{{orderInfo.status=="FINISH"?'':''}}</span>
      </div>
      <div class="invoice-info-content">
        <span></span>
        <span>{{splitNum(orderInfo.price)}}</span>
      </div>
      <div class="invoice-info-content">
        <span></span>
        <span>{{invoiceInfo.gmtCreate}}</span>
      </div>
      <div class="invoice-info-content">
        <span></span>
        <span>{{invoiceInfo.medium=="PAPER"?'':''}}</span>
      </div>
      <div class="invoice-info-content">
        <span></span>
        <span>{{invoiceInfo.status=="NO"?"":invoiceInfo.status=="ING"?"":""}}</span>
      </div>
      <div class="invoice-info-content">
        <span></span>
        <span>{{invoiceInfo.titleType=="COMPANY"?"":invoiceInfo.titleType=="INDIVIDUAL"?"":""}}</span>
      </div>
      <div class="invoice-info-content">
        <span></span>
        <span>{{invoiceInfo.title}}</span>
      </div>
      <div class="invoice-info-content">
        <span></span>
        <span>{{invoiceInfo.taxNo}}</span>
      </div>
      <div class="invoice-info-content">
        <span></span>
        <span>{{invoiceInfo.receiverName}}</span>
      </div>
      <div class="invoice-info-content">
        <span></span>
        <span>{{invoiceInfo.receiverPhone}}</span>
      </div>
      <div class="invoice-info-content">
        <span></span>
        <span>{{invoiceInfo.receiverAddress}}</span>
      </div>
    </div>
    <!--  -->
    <h4></h4>
    <div class="invoice-info" v-for="item in orderInfo.goods" :key="item.id">
      <div class="invoice-info-content">
        <span></span>
        <span>{{item.goodsName}}</span>
      </div>
      <div class="invoice-info-content">
        <span></span>
        <span>{{item.count}}</span>
      </div>
      <div class="invoice-info-content">
        <span></span>
        <span>{{splitNum(item.originPrice)}}</span>
      </div>

      <div class="invoice-info-content">
        <span></span>
        <span>{{splitNum(item.goodsPrice)}}</span>
      </div>
    </div>
    <div style="margin-top:30px;">
      <el-button type="primary" @click="auditBtn" size="medium"></el-button>
      <el-button type="info" @click="$router.go(-1)" style="margin-left:40px;" size="medium"></el-button>
    </div>
    <!--  -->
    <!-- <el-dialog title="" :visible.sync="dialogFormVisible" append-to-body width="30%">
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="makeOutInvice()"> </el-button>
      </div>
    </el-dialog>-->
  </div>
</template>
<script>
import { mapState } from "vuex";
import http from "http/invoiceManageApi";
export default {
  name: "invoiceAudit",
  data() {
    return {
      invoiceInfo: {},
      orderInfo: {},
      dialogFormVisible: false
    };
  },
  activated() {
    // console.log(JSON.parse(this.$route.query.invoiceInfo))
    this.invoiceInfo = JSON.parse(this.$route.query.invoiceInfo);
    this.getOrderInfo();
  },
  computed: {
    ...mapState(`user`, [`access_token`])
  },
  methods: {
    getOrderInfo() {
      let params = {
        orderNo: this.invoiceInfo.orderNo,
        access_token: this.access_token
      };
      http.getOrderInfo({ params }).then(data => {
        this.orderInfo = data.data.data;
      });
    },
    // 
    splitNum(num) {
      if (typeof num !== "number" || isNaN(num)) return null;
      return num / 100.0;
    },
    // 
    auditBtn() {
      let params = {
        invoiceId: this.invoiceInfo.id,
        access_token: this.access_token,
        content: '',
        status: "YES"
      };
let that = this
      //   this.dialogFormVisible  = true
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
            http.auditInvoice(params).then(data=>{
            console.log(data.data)
            if(data.data.code==0){
              that.$message(data.data.desc)
              setTimeout(()=>{
                  that.$router.go(-1)
              },2000)
            }else {
              that.$message(data.data.desc)
            }
          }).catch(e=>{
              this.$message(``)
          })
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
    // makeOutInvice(){
    //      let params = {invoiceId:this.invoiceInfo.id,access_token:this.access_token,content:this.invoiceForm.content,status:"YES"}
    // },
  }
};
</script>
<style lang="stylus" scoped>
.invoice-info-content {
  line-height: 28px;
  display: flex;
  align-items: center;
}

.invoice-info-content>span {
  display: block;
  margin-right: 14px;
}

.invoice-info-content>span:nth-child(1) {
  width: 120px;
}
</style>


