<template>
  <div id="installTask">
    <el-form
      :inline="true"
      :model="taskInfo"
      size="medium"
      label-width="120px"
      label-suffix=""
      class="demo-form-inline"
      ref="taskInfo"
    >
      <el-form-item label="">
        <el-select v-model="taskInfo.stationId" clearable>
          <!-- :key="item.value" :label="item.sex" :value="item.value" -->
          <el-option
            v-for="item in storeOptions"
            :key="item.stationId"
            :label="item.stationName"
            :value="item.stationId"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="taskInfo.isGrab" clearable>
          <!-- :key="item.value" :label="item.sex" :value="item.value" -->
          <el-option
            v-for="item in isGrabOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="taskInfo.taskStatus" clearable>
          <!-- :key="item.value" :label="item.sex" :value="item.value" -->
          <el-option
            v-for="item in statusOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="taskInfo.takeType" clearable>
          <!-- :key="item.value" :label="item.sex" :value="item.value" -->
          <el-option
            v-for="item in typeOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="taskInfo.cooperStatus" clearable>
          <!-- :key="item.value" :label="item.sex" :value="item.value" -->
          <el-option
            v-for="item in cooperStatusOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-button type="primary" @click="getTaskList" size="medium"></el-button>
    </el-form>
    <!--  -->
    <el-table :data="taskList" border style="width: 100%;margin-top:14px;">
      <el-table-column label="" width="58" fixed>
        <template slot-scope="scope">
          <span>{{indexMethods(scope.$index)}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="bookId" label="" fixed></el-table-column>
      <el-table-column prop="taskId" label=""></el-table-column>
      <el-table-column prop="stationId" label=""></el-table-column>
      <el-table-column prop="orderNo" label=""></el-table-column>
      <el-table-column prop="goodModel" label=""></el-table-column>
      <el-table-column prop="goodName" label=""></el-table-column>
      <el-table-column prop="userId" label=""></el-table-column>
      <el-table-column prop="userName" label=""></el-table-column>
      <el-table-column prop="userPhone" label=""></el-table-column>
      <el-table-column prop="grabTech" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span>{{scope.row.cooperStatus==0?'':scope.row.cooperStatus==1?'':''}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="confirmedNum" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span>{{scope.row.takeType==0?'':scope.row.takeType==1?'':''}}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span>{{scope.row.taskStatus==1?'':scope.row.taskStatus==2?'':scope.row.taskStatus==3?'':scope.row.taskStatus==4?'':scope.row.taskStatus==5?'':scope.row.taskStatus==7?'':''}}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <img :src="scope.row.driverLicense" class="driverImg" alt>
        </template>
      </el-table-column>
      <el-table-column prop="bookTime" label=""></el-table-column>
      <el-table-column prop="confirmTime" label=""></el-table-column>
      <el-table-column prop="completeTime" label=""></el-table-column>
      <!-- <el-table-column label="">
        <template slot-scope="scope">
          <img v-for="(item,index) in scope.row.pictures" class="driverImg" :key="index" :src="item.picturePath" alt>
        </template>
      </el-table-column>-->
      <!-- <el-table-column fixed="right" label="">
        <template slot-scope="scope">
          <el-button @click="editPersonnel(scope.row,'checked')" type="text" size="small"></el-button>
          <el-button @click="deletePersonel(scope.row,'checked')" type="text" size="small"></el-button>
        </template>
      </el-table-column>-->
    </el-table>
    <!--  -->
    <el-pagination
      background
      layout="prev, pager, next"
      :page-size="taskInfo.size"
      :current-page="taskInfo.page"
      @current-change="handlePageNumChange"
      :total="total"
    ></el-pagination>
  </div>
</template>
<script>
import { mapState } from "vuex";
import http from "http/taskApi";
import https from "http/personnelApi";
export default {
  data() {
    return {
      taskInfo: {
        stationId: ``,
        page: 1,
        size: 6,
        isGrab: ``, //   0-  1-
        taskStatus: ``, //1-2-3-4-5-7-
        takeType: ``, //0-1-
        cooperStatus: ``, //0->0-
        access_token: ``
      },
      taskList: [],
      total: 0,
      storeOptions: [],
      isGrabOptions: [{ value: "0", label: `` }, { value: "1", label: `` }],
      statusOptions: [
        { value: "1", label: "" },
        { value: "2", label: "" },
        { value: "3", label: "" },
        { value: "4", label: "" },
        { value: "5", label: "" },
        { value: "7", label: "" }
      ],
      typeOptions: [
        { value: "0", label: "" },
        { value: "1", label: "" }
      ],
      cooperStatusOptions: [
        { value: "0", label: "" },
        { value: "1", label: "" }
      ]
    };
  },
  computed: {
    ...mapState(`user`, [`access_token`])
  },
  activated() {
    this.taskInfo.access_token = this.access_token;
    this.getStoreList();
    this.getTaskList();
  },
  methods: {
    //
    getStoreList() {
      console.log();
      let params = { page: 1, size: 0, access_token: this.access_token };
      https.storeList({ params }).then(data => {
        console.log(data);
        this.storeOptions = data.data.data;
      });
    },
    getTaskList() {
      let taskInfo = this.taskInfo;
      let value = {
        access_token: this.access_token,
        page: taskInfo.page,
        size: taskInfo.size
      };
      let params = {
        stationId: taskInfo.stationId,
        isGrab: taskInfo.isGrab,
        taskStatus: taskInfo.taskStatus,
        takeType: taskInfo.takeType,
        cooperStatus: taskInfo.cooperStatus
      };
      http.getInstallList(params, value).then(data => {
        console.log(data.data.data);
        if (data.data.code == 0) {
          this.taskList = data.data.data;
          this.total = data.data.total;
        } else {
          this.$message(data.data.desc);
        }
      });
    },
    indexMethods(index) {
      return (this.taskInfo.page - 1) * this.taskInfo.size + index + 1;
    },
    handlePageNumChange(num) {
      this.taskInfo.page = num;
      this.getTaskList();
    }
  }
};
</script>
<style lang="stylus" scoped>
.driverImg {
  width: 100%;
}

.el-pagination {
  margin-top: 20px;
  display: flex;
  justify-content: flex-end;
}
</style>




